.main-title {
    /* font-family: 'Amaranth', sans-serif; */
    font-family: 'Cabin Sketch', cursive;
    font-size: 4.8em;
}

.home-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('https://source.unsplash.com/Wmuu76rFl0Y/800x600');
    /* background-image: url('https://source.unsplash.com/LLcAJbEh1_Q/800x600'); */
}

.search-container {
    display: flex;
    align-items: center;
}

.search-box-container {
    margin: 1.5em;
}

.home-search-box {
    height: 2.5em;
}

.search-button {
    padding: 0.5em 0.7em;
    /* margin: 1em; */
    border-radius: 0.5em;
    font-size: 1.4em;
    background-color: rgb(255, 149, 11);
    border: none;
}

.buttons-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-button {
    padding: 1em 2em;
    margin: 1em;
    border-radius: 0.5em;
    background-color: papayawhip;
    font-size: 1.4em;
    font-weight: bold;
    border: none;
}

@media(min-width: 768px) {
    .home-screen {
        background-image: url('https://source.unsplash.com/Wmuu76rFl0Y/1200x1000');
    }
}

@media(min-width: 1200px) {
    .home-screen {
        background-image: url('https://source.unsplash.com/Wmuu76rFl0Y/1400x600');
    }
}
.map-container {
    width: 85%;
    height: 300px;
    margin: auto;
}

.search-input-container {
    display: flex;
    justify-content: space-evenly;
    margin: 1rem;
}

.search-box {
    box-sizing: border-box;
    border: 1 px solid transparent;
    width: 200px;
    border: 1px solid transparent;
    border-radius: 3px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    font-size: 12px;
    outline: none;
    text-overflow: ellipses;
    padding: 2px 10px;
}

